import {mapState} from 'vuex'
import {get,post} from '@/common/methods.js';
import {
	onlineURL
} from '@/common/common.js';
import Header from '@/components/header/header.vue';
export default {
	data() {
		return {
			enterpriseName:'',
			cold: '',
			name: '',
			workNumber: '',
			dept: '',
			imageList: [], //保存图片路径集合
			imageLength: 1, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
			imgPath:'',
			sourceType : [
				['camera'], //拍照
				['album'], //相册
				['camera', 'album'] //拍照或相册
			],
			sizeType : [
				['compressed'], //压缩
				['original'], //原图
				['compressed', 'original'] //压缩或原图
			]
		};
	},
	computed: {
		...mapState([
			"userid"
		]),
	},
	onLoad(e) {
		this.enterpriseName = e.enterpriseName;
		this.cold = e.enterpriseLid;
		//console.log(this.enterpriseName);
	},
	methods: {
		apply() {
			let requiredInfo = {
				"associatorid": this.userid,
				"coId": this.cold,
				"name": this.name,
				"dept": this.dept,
				"code": this.workNumber,
				"path": this.imgPath
			}
			let namereg = /^[\u4e00-\u9fa5 ]{2,20}$/;
			if ( !namereg.test(this.name) ) {
				uni.showToast({
					title: '真实姓名格式不正确',
					duration: 2000,
					icon: 'none',
				});
			}else if(this.name == ''){
				uni.showToast({
					title: '真实姓名不能为空',
					duration: 2000,
					icon: 'none',
				});
			}else if( this.workNumber == ''){
				uni.showToast({
					title: '工作证件不能为空',
					duration: 2000,
					icon: 'none',
				});
			}else if( this.dept == ''){
				uni.showToast({
					title: '部门不能为空',
					duration: 2000,
					icon: 'none',
				});
			}else if( this.imgPath == '' ){
				uni.showToast({
					title: '请上传证件',
					duration: 2000,
					icon: 'none',
				});
			}else if(this.userid == null){
				uni.showToast({
					title: '请先登录',
					duration: 2000,
					icon: 'none',
				});
			}else{
				uni.request({
					url: onlineURL + '/enterprise/associator-company/add',
					method: 'POST',
					data: requiredInfo,
					// header: {
					// 	'content-type': 'application/json'
					// },
					success: res => {
						console.log(res);
						uni.reLaunch({
							url: './submitted'
						});
					},
					
				});
			}
		},
		//选择图片
		chooseImage: async function() {
				uni.chooseImage({
					sourceType: this.sourceType[this.sourceTypeIndex],

					sizeType: this.sizeType[this.sizeTypeIndex],

					count: this.imageLength - this.imageList.length,
					success: (res) => {
						console.log(res);
						this.imageList = this.imageList.concat(res.tempFilePaths);
						uni.uploadFile({
							url: 'http://192.168.0.210:9999/enterprise/associator-company/upload',
							filePath: this.imageList[0],
							name: 'file',
							formData: {
								'user': 'test'
							},
							success: (uploadFileRes) => {
								console.log(JSON.parse(uploadFileRes.data).data);
								this.imgPath = JSON.parse(uploadFileRes.data).data;
							}
						});
					}
				})
			},
			//预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			//删除图片
			deleteImage: function(e) {
				var index = e.target.dataset.index;
				var that = this;
				var images = that.imageList;
				images.splice(index, 1);
				that.imageList = images;
				this.imgPath = '';
			}
	},
	components: {
		Header
	}
}
